.header {
  background-color: white;
  display: flex;
  justify-content: space-between;
  height: 3em;
  align-items: center;
}
.nav {
  position:relative;
  margin-right: 1em;
  ul {
    display: flex;
    list-style: none;
    gap: 2em;
    padding: 0;
    margin: 0;
  }
  a {
    color: black;
    text-decoration: none;
    font-size: large;
  }
}
.logo{
  margin-left: 1em;
}
.mobileMenuButton {
  display: none;
}
@media (max-width: 768px) {
  .mobileMenuButton {
    display: block;
    margin-right: 1em;
  }

  .nav {
    display: none;
  }

  .nav.expanded {
    display: block;
    position: absolute;
    top:3em;
    width: 100%;
    ul{
      
      display:grid;
      background-color: white;
    }
    a{
      display: block;
      width: 100%;
      text-align: center;
      text-decoration: none;
      font-size: large;
      padding: 2em 0;
    }

  }
}